@import "assets/scss/variables";
@import "assets/scss/mixins";

.educationList {
  @include dflex(space-between, stretch, row, wrap);

  margin: 50px 0;

  &__item {
    @include dflex(flex-start, center, row, nowrap);

    width: 48%;
    margin-bottom: 20px;
    list-style-type: none;

    &_date {
      margin-right: 15px;
    }

    &_content {
      .item__place {
        font-weight: bold;
        font-size: 20px;
      }

      .item__specialization {
        font-size: $textSize;
        color: $grey;
      }
    }
  }
}

/*   media   start */
/*     < 991    */
@include tablet() {
  .educationList {
    width: 100%;
    max-width: 540px;
    margin: 30px auto;

    &__item {
      width: 100%;
    }
  }
}
/*     < 991    */


/*     < 768    */
@include small-tablet() {
  .educationList {
    max-width: 490px;

    &__item {
      &_date {
        margin-right: 10px;
      }

      &_content {
        .item__place {
          font-size: 18px;
        }
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .educationList {
    margin: 20px 0;

    &__item {
      flex-direction: column;
      align-items: center;
      justify-content: center;

      &_date {
        margin: 0 0 5px;
      }

      &_content {
        text-align: center;

        .item__place {
          font-size: 16px;
        }
      }
    }
  }
}
/*  <    575    */
/*   media   end */
