@import "assets/scss/mixins";

.paginationList {
  @include dflex(center, stretch, row, wrap);

  flex-wrap: wrap;
  margin: 40px auto 0;
}

/*   media   start */
/*     < 768    */
@include small-tablet() {
  .paginationList {
    margin-top: 30px;
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .paginationList {
    margin-top: 25px;
  }
}
/*  <    575    */
/*   media   end */
