@import "assets/scss/variables";
@import "assets/scss/mixins";

.experienceList {
  width: fit-content;
  margin: 0 auto;

  & > .experienceList__item {
    list-style-type: none;
  }

  &__item {
    @include dflex(flex-start, center, row, wrap);

    padding: 10px;
    border-radius: $borderRadius;
    background-color: $mainDarkColor2;

    & ~ .experienceList__item {
      margin-top: 50px;
    }

    &_date {
      margin-right: 15px;
    }

    &_info {
      flex-grow: 1;
      line-height: 1.2;
    }

    .item__place {
      font-weight: bold;
      font-size: 24px;
    }

    .item__position {
      margin-top: 5px;
      font-size: 20px;
      color: $green;
    }

    .responsibilities__title {
      width: 100%;
      margin-top: 10px;
      font-size: $textSize;
    }

    .responsibilitiesList {
      padding-left: 10px;
      font-size: $textSize;
      line-height: 1.2;
      color: lighten($grey, 20);

      li {
        position: relative;
        z-index: 1;
        padding-left: 10px;

        & ~ li {
          margin-top: 3px;
        }

        &:before {
          @include psevdo(7px);

          left: 0;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: $orange;
        }
      }
    }
  }
}


/*   media   start */
/*     < 768    */
@include small-tablet() {
  .experienceList {
    &__item {
      & ~ .experienceList__item {
        margin-top: 30px;
      }

      &_date {
        margin-right: 10px;
      }

      .item__place {
        font-size: 22px;
      }

      .item__position {
        font-size: 18px;
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .experienceList {
    &__item {
      align-items: center;
      justify-content: center;

      & ~ .experienceList__item {
        margin-top: 25px;
      }

      &_date {
        margin: 0 0 10px;
      }

      &_info {
        width: 100%;
        text-align: center;
      }

      .item__place {
        font-size: 18px;
      }

      .item__position {
        font-size: 16px;
      }

      .responsibilities__title {
        text-align: center;
      }
    }
  }
}
/*  <    575    */
/*   media   end */
