@import "assets/scss/variables";
@import "assets/scss/mixins";

.footer {
  position: relative;
  z-index: 1;
  width: 100%;
  padding: 20px 0;
  border-top: $headFootBorder;
  background-color: $mainDarkColor;
  background-image: $mainGradient;

  .footer__wrapper {
    @include dflex(space-between, center);
  }

  .footer__link {
    width: 30px;
    height: 30px;
    background: url(../../assets/images/github.png) no-repeat center / contain;
    transform: scale(1);

    &:hover {
      transform: scale(1.2);
    }
  }

  .footer__copyright {
    font-size: $textSize;
    color: $white;
  }
}

/*   media   start */
/*     < 1100    */
@include desktop() {
  .footer {
    padding: 15px 0;
  }
}
/*     < 1100    */
/*   media   end */
