@import "assets/scss/variables";
@import "assets/scss/mixins";

$sizeIcon: 25px;

.contactsList {
  display: block;
  width: fit-content;
  max-width: 100%;

  &__item {
    list-style-type: none;

    &:not(:first-child) {
      margin-top: 20px;
    }
  }

  &__link {
    position: relative;
    z-index: 1;
    padding-left: calc(#{$sizeIcon} + 10px);
    font-size: 20px;
    line-height: $sizeIcon;
    color: $white;

    &:hover {
      color: $orange;
    }

    &:after,
    &:before {
      position: absolute;
      top: 0;
      left: 0;
      width: $sizeIcon;
      height: $sizeIcon;
    }

    &:before {
      content: "";
      border-radius: 3px;
      box-shadow: 0 0 10px rgba(50, 50, 50, 0.5);
      background: $white;
    }

    &:after {
      font-size: $sizeIcon;
      line-height: 1;
      text-align: center;
      color: $black;
    }
  }
}

/*   media   start */
/*  <    575    */
@include mobile() {
  .contactsList {
    &__link {
      font-size: 18px;
    }
  }
}
/*  <    575    */
/*   media   end */
