@import "assets/scss/variables";
@import "assets/scss/mixins";

.paginationList__item {
  margin: 5px;
  line-height: 1;
  text-align: center;

  span,
  button {
    display: block;
    height: 100%;
    min-width: 30px;
    padding: 5px;
    font-size: $textSize;
    border-radius: $borderRadius;
    border-width: 2px;
    border-style: solid;
    color: $white;
  }

  &_current {
    border-color: $white;
    background-color: $mainDarkColor;
  }

  &_button {
    border-color: $grey;
    background-color: $grey;
  }
}

/*   media   start */
/*  <    575    */
@include mobile() {
  .paginationList__item {
    span,
    button {
      font-size: 14px;
    }
  }
}
/*  <    575    */
/*   media   end */
