@import "assets/scss/variables";
@import "assets/scss/mixins";

@include font-face("PressStart2P", "../fonts/PressStart2P-Regular");
@include font-face("Roboto", "../fonts/RobotoRegular");
@include font-face("Roboto", "../fonts/RobotoMedium", 600);
@include font-face("Roboto", "../fonts/RobotoBold", bold);

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font: 16px / 1.4 Roboto, sans-serif;
}

:root {
  --header-size: 0;
  --footer-size: 0;
  --top-section: 0;
}

body {
  position: relative;
  z-index: 1;
  margin: 0;
  padding: 0;
  color: $white;
  background-color: $mainDarkColor;
  background-image: $mainGradient;

  &:before {
    @include psevdo;

    inset: 0;
    background: url(../images/bg.png) repeat;
    background-attachment: fixed;
    opacity: 0.3;
  }
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

section {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
figure {
  margin: 0;
  padding: 0;
}

ul li {
  list-style-type: none;
}

.page-title,
.title {
  font-family: "PressStart2P", sans-serif;
  text-align: center;
  color: $green;

  span {
    color: $orange;
  }
}

.page-title {
  margin-bottom: 50px;
  font-size: 40px;
}

.title {
  margin-bottom: 40px;
  font-size: 36px;
}

a {
  display: inline-block;
  text-decoration: none;
  color: $whiteGrey;
  transition: 0.3s;

  &:hover {
    color: $orange;
  }
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

button {
  display: block;
  transition: 0.3s;
  cursor: pointer;
}

canvas {
  display: block;
}

::-webkit-scrollbar {
  width: 12px;
  background-color: $mainDarkColor2;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: $orange;
}

.grid-list {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-flow: dense;

  .grid-list__item {
    list-style-type: none;

    &:nth-child(6n + 1),
    &:nth-child(6n + 5) {
      grid-row-end: span 2;
    }
  }

  .grid-list__link {
    position: relative;
    z-index: 1;
    display: flex;
    width: 100%;
    height: 100%;
    border-radius: $borderRadius;
    border: 1px solid $grey;
    overflow: hidden;
    perspective: 800px;

    &:hover {
      &:before {
        opacity: 0;
      }

      .grid-list__link_img {
        filter: blur(0);
      }

      .grid-list__link_description {
        transform: rotateX(0);
      }
    }

    &:before {
      @include psevdo;

      inset: 0;
      background-color: transparentize($mainDarkColor, 0.6);
      opacity: 1;
      transition: opacity $transition;
    }

    &_img {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      z-index: -2;
      object-fit: cover;
      filter: blur(3px);
      transition: filter $transition;
    }

    &_description {
      width: 100%;
      margin-top: auto;
      padding: 10px;
      border-radius: $borderRadius;
      background-color: transparentize($mainDarkColor, 0.1);
      transition: transform $transition;
      transform: rotateX(60deg);
      transform-origin: bottom left;
    }
  }
}

/*   media   start */
/*     < 1300    */
@include xxl-desktop() {
  .page-title {
    font-size: 36px;
  }

  .title {
    font-size: 32px;
  }
}
/*     < 1300    */

/*     < 1100    */
@include desktop() {
  .page-title {
    font-size: 34px;
  }

  .title {
    font-size: 30px;
  }

  .grid-list {
    grid-gap: 30px;
  }
}
/*     < 1100    */

/*     < 991    */
@include tablet() {
  .page-title {
    margin-bottom: 30px;
    font-size: 30px;
  }

  .title {
    margin-bottom: 30px;
    font-size: 28px;
  }

  .grid-list {
    grid-gap: 20px;

    .grid-list__link {
      &_description {
        transform: rotateX(30deg);
      }
    }
  }
}
/*     < 991    */

/*     < 768    */
@include small-tablet() {
  .page-title {
    font-size: 28px;
  }

  .title {
    font-size: 26px;
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .page-title {
    margin-bottom: 20px;
    font-size: 26px;
  }

  .title {
    margin-bottom: 20px;
    font-size: 24px;
  }

  .grid-list {
    display: block;

    .grid-list__link {
      & ~ .grid-list__link {
        margin-top: 20px;
      }
    }
  }
}
/*  <    575    */
/*   media   end */
