$white: #fff;
$mainDarkColor: #010101;
$mainDarkColor2: #2e2e2e;
$black: #1a1a1a;
$blue: #0061fd;
$green: #28c700;
$orange: #ff5522;
$grey: #7e7e7e;
$withoutColor: transparent;
$yellow: #f7b733;

$gradientBlack1: $yellow;
$gradientBlack2: $orange;

$bgColor1: #15171d;
$mainGradient: linear-gradient(to left, $mainDarkColor, $bgColor1);
$gradientBlack: linear-gradient(to right, $mainDarkColor, $mainDarkColor2);

$headFootBorder: 1px solid transparentize($white, 0.9);

$red: #ed0b0e;
$lightOrange: #d6aa81;
$whiteGrey: #e4e4e4;

$maxContentWidth: 1300px;
$textSize: 16px;
$borderRadius: 5px;
$transition: 0.3s;
$pagePadding: 70px 0;
$pagePaddingTablet: 40px 0;
$pagePaddingMobile: 25px 0;

%bg-curtain1 {
  background: $gradientBlack1;
  background: linear-gradient(to right, $gradientBlack1, $gradientBlack2);
}

%bg-curtain2 {
  background: $gradientBlack2;
  background: linear-gradient(to right, $gradientBlack2, $gradientBlack1);
}

%bg-dark {
  box-shadow: 0 0 20px transparentize($mainDarkColor, 0.7);
  background: $mainGradient;
}
