@import "assets/scss/variables";
@import "assets/scss/mixins";

.skillsList {
  @include dflex(center, stretch, row, wrap);

  margin-bottom: 50px;

  &__item {
    @include dflex(flex-start, center);
    @extend %bg-dark;

    margin: 0 15px 30px;
    padding: 10px;
    width: fit-content;
    max-width: 100%;
    border-radius: $borderRadius;
    list-style-type: none;

    &_image {
      width: 50px;
      height: 50px;
      object-fit: contain;
    }

    &_name {
      font-size: 20px;
    }

    &_level {
      margin-left: 15px;

      .levelItem {
        width: 20px;
        height: 5px;
        border-radius: $borderRadius;
        background-color: $orange;

        &.active {
          background-color: $green;
        }

        & ~ .levelItem {
          margin-top: 3px;
        }
      }
    }
  }
}

/*   media   start */
/*     < 768    */
@include small-tablet() {
  .skillsList {
    margin-bottom: 40px;

    &__item {
      margin: 0 10px 20px;

      &_image {
        width: 40px;
        height: 40px;
      }

      &_name {
        font-size: 18px;
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .skillsList {
    margin-bottom: 30px;

    &__item {
      padding: 8px;

      &_level {
        margin-left: 10px;
      }
    }
  }
}
/*  <    575    */
/*   media   end */
