@import "assets/scss/variables";
@import "assets/scss/mixins";

.interval {
  @include dflex(flex-start, center, row, wrap);
  @extend %bg-dark;

  flex-shrink: 0;
  padding: 10px;
  border-radius: $borderRadius;

  &__start {
    margin-right: 3px;
    font-size: 16px;
    line-height: 1;

    .year {
      writing-mode: vertical-lr;
      transform: scale(-1, -1);
    }
  }

  &__end {
    font-weight: bold;
    font-size: 46px;
    line-height: 1;
    text-transform: uppercase;
    color: $orange;
  }
}

/*   media   start */
/*     < 1100    */
@include desktop() {
  .interval {
    &__end {
      font-size: 42px;
    }
  }
}
/*     < 1100    */

/*     < 768    */
@include small-tablet() {
  .interval {
    padding: 10px;

    &__start {
      font-size: 16px;
    }

    &__end {
      font-size: 40px;
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .interval {
    padding: 6px;

    &__end {
      font-size: 34px;
    }
  }
}
/*  <    575    */
/*   media   end */
