@import "assets/scss/variables";
@import "assets/scss/mixins";

@keyframes topDown {
  0%,
  100% {
    left: 0;
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    left: -2px;
  }

  20%,
  40%,
  60%,
  80% {
    left: 2px;
  }
}

$burgerLineTranslate: 7px;

.header {
  position: fixed;
  z-index: 10;
  width: 100%;
  padding: 15px 0;
  border-bottom: $headFootBorder;
  color: $white;
  background-color: $mainDarkColor;
  background-image: $mainGradient;

  .header__wrapper {
    @include dflex(space-between, center);
  }

  .header__logo {
    @include dflex(space-between, flex-start);

    font-size: 45px;
    line-height: 1;
    font-family: "PressStart2P", sans-serif;

    .header__logo_color {
      margin-top: 5px;
      color: $orange;
      transition: $transition;
    }

    &:hover {
      color: $orange;

      .header__logo_color {
        color: $white;
      }
    }
  }

  .navigation__list {
    @include dflex(center, center);

    &_item {
      margin: 0 5px;
    }

    &_link {
      position: relative;
      z-index: 1;
      display: block;
      padding: 5px 25px;
      font-weight: bold;
      font-size: $textSize;
      color: $black;

      &.active {
        color: $orange;

        &:before,
        &:after {
          transform: rotate(0);
        }

        &:before {
          top: -5px;
          background-color: $orange;
        }
      }

      &.disabled {
        cursor: not-allowed;

        &:before,
        &:after {
          background-color: $grey;
          transform: rotate(0);
        }
      }

      &:not(.active, .disabled):hover {
        color: $orange;

        &:before,
        &:after {
          transform: rotate(0);
          animation: topDown 3s infinite;
        }

        &:before {
          top: -5px;
          animation-direction: reverse;
        }
      }

      &:before,
      &:after {
        @include psevdo;

        left: 0;
        width: 100%;
        height: 100%;
        border-radius: $borderRadius;
        transition: $transition;
        pointer-events: none;
      }

      &:before {
        background-color: $green;
        transform: rotate(8deg);
      }

      &:after {
        background-color: $white;
        transform: rotate(-8deg);
      }
    }
  }

  .burger {
    display: none;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    background-color: transparent;

    span {
      position: relative;
      display: block;
      width: 100%;
      height: 3px;
      border-radius: $borderRadius;
      background-color: $orange;

      &:before,
      &:after {
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: inherit;
        border-radius: $borderRadius;
        background-color: $orange;
      }

      &:before {
        top: -#{$burgerLineTranslate};
        transition: 0.8s;
      }

      &:after {
        bottom: -#{$burgerLineTranslate};
        transition: 0.4s;
      }
    }

    &.active_burger {
      span {
        background: none;

        &:before {
          transform: translateY(#{$burgerLineTranslate}) rotate(-405deg);
        }

        &:after {
          transform: translateY(-#{$burgerLineTranslate}) rotate(45deg);
        }
      }
    }
  }
}

/*   media   start */
/*     < 1300    */
@include xxl-desktop() {
  .header {
    padding: 10px 0;

    .header__logo {
      font-size: 38px;
    }
  }
}
/*     < 1300    */

/*     < 1100    */
@include desktop() {
  .header {
    .header__logo {
      font-size: 32px;

      .header__logo_color {
        margin-top: 3px;
      }
    }

    .navigation__list {
      &_link {
        padding: 5px 20px;
        font-size: 14px;
      }
    }
  }
}
/*     < 1100    */

/*     < 991    */
@include tablet() {
  .header {
    .navigation {
      position: fixed;
      top: 0;
      left: -250%;
      z-index: 100;
      width: 100%;
      height: 100%;
      padding: 0;
      background-color: transparentize($black, 0.5);
      transition: calc($transition * 2);

      &.active_menu {
        left: 0;
        overflow-y: auto;
      }

      &:before {
        @include psevdo;

        left: 0;
        width: 300px;
        height: 100%;
        background-color: $black;
      }
    }

    .navigation__list {
      flex-direction: column;
      width: 100%;
      max-width: 300px;

      &_item {
        width: 100%;
        text-align: center;
        margin: 0;
        border-bottom: 1px solid transparentize($white, 0.5);

        & ~ .navigation__list_item {
          margin-top: 5px;
        }
      }
      &_link {
        font-size: 16px;
        color: $white;

        &:before,
        &:after {
          display: none;
        }
      }
    }

    .burger {
      z-index: 101;
      display: block;
    }
  }
}
/*     < 991    */

/*  <    575    */
@include mobile() {
  .header {
    .header__logo {
      font-size: 28px;
    }

    .navigation {
      &:before {
        width: 270px;
      }
    }

    .navigation__list {
      max-width: 270px;
    }
  }
}
/*  <    575    */
/*   media   end */
