@import "assets/scss/mixins";
@import "assets/scss/variables";

.notFound {
  text-align: center;
}

.error_title {
  margin-bottom: 50px;
  font: bold 100px / 1 "PressStart2P", sans-serif;
  text-align: center;
  color: $green;

  span {
    color: $orange;
  }
}

.error_text {
  font-size: 26px;
}

/*   media   start */
/*     < 1100    */
@include desktop() {
  .error_title {
    font-size: 90px;
  }

  .error_text {
    font-size: 24px;
  }

}
/*     < 1100    */

/*     < 991    */
@include tablet() {
  .error_title {
    margin-bottom: 40px;
    font-size: 80px;
  }

  .error_text {
    font-size: 20px;
  }

}
/*     < 991    */

/*  <    575    */
@include mobile() {
  .error_title {
    font-size: 50px;
  }

  .error_text {
    font-size: 18px;
  }
}
/*  <    575    */
/*   media   end */

