@import "assets/scss/variables";
@import "assets/scss/mixins";

.projectsList {
  &__item {
    width: 100%;
    height: 100%;
    color: $grey;
    list-style-type: none;
    overflow: hidden;
  }

  &__link {
    padding-top: 220px;

    &:hover {
      .projectsList__link_name {
        transform: rotateX(0);
      }
    }

    &_name {
      font-size: 20px;
      color: $green;
    }
  }
}



/*   media   start */
/*     < 1100    */
@include desktop() {
}
/*     < 1100    */

/*     < 991    */
@include tablet() {
  .projectsList {
    &__link {
      padding-top: 140px;
    }
  }
}
/*     < 991    */

/*     < 768    */
@include small-tablet() {
  .projectsList {
    &__link {
      padding-top: 120px;

      &_name {
        font-size: 18px;
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .projectsList {
    max-width: 350px;
    margin: 0 auto;

    &__link {
      padding-top: 120px;
      margin-bottom: 20px;

      &_name {
        font-size: 16px;
      }
    }
  }
}
/*  <    575    */
/*   media   end */
