@import "assets/scss/variables";
@import "assets/scss/mixins";

%bg-styles {
  @extend %bg-dark;

  border-radius: 50%;
}

.home {
  color: $white;

  .home__wrapper {
    display: grid;
    grid-gap: 20px;
    grid-template-areas:
      "photo info"
      "photo  info"
      "contacts  contacts";
    grid-template-columns: 500px 2fr;
    grid-template-rows: repeat(3, auto);
  }

  .home__author {
    grid-area: photo;
    width: 480px;
    height: 480px;
  }

  .home__content {
    grid-area: info;

    &_start {
      padding-left: 20px;
      font-size: 24px;
      line-height: 1;
    }

    &_name {
      margin: 10px 0 30px;
      font: 400 38px / 1 PressStart2P, sans-serif;
      color: $orange;
      overflow: hidden;

      span {
        display: inline-block;
      }
    }

    &_position {
      margin-bottom: 60px;
      font-weight: bold;
      font-size: 36px;
      line-height: 1;
    }

    .home__content_advantages {
      margin-bottom: 60px;
    }

    &_text {
      font-size: $textSize;

      & ~ .home__content_text {
        margin-top: 20px;
      }
    }
  }

  .home__contacts {
    @include dflex(flex-start, center);

    grid-area: contacts;
    margin-top: 50px;

    .listContacts {
      @include dflex(center, center);

      margin-right: 30px;

      li {
        margin: 0 10px;
      }
    }
  }
}

/*   media   start */
/*     < 1300    */
@include xxl-desktop() {
  .home {
    .home__wrapper {
      grid-gap: 10px;
      grid-template-columns: 450px 2fr;
    }

    .home__author {
      width: 450px;
      height: 450px;
    }

    .home__content {
      &_start {
        font-size: 22px;
      }

      &_name {
        font-size: min(2.6vw, 36px);
      }

      &_position {
        margin-bottom: 50px;
        font-size: 30px;
      }

      .home__content_advantages {
        margin-bottom: 50px;
      }
    }
  }
}
/*     < 1300    */

/*     < 1100    */
@include desktop() {
  .home {
    .home__wrapper {
      grid-template-columns: 400px 2fr;
    }

    .home__author {
      width: 400px;
      height: 400px;
    }

    .home__content {
      &_name {
        margin-bottom: 20px;
        font-size: 2.6vw;
      }

      &_position {
        margin-bottom: 40px;
        font-size: 28px;
      }

      .home__content_advantages {
        margin-bottom: 40px;
      }
    }

    .home__contacts {
      margin-top: 30px;
    }
  }
}
/*     < 1100    */

/*     < 991    */
@include tablet() {
  .home {
    .home__wrapper {
      display: block;
    }

    .home__author {
      width: 340px;
      height: 340px;
    }

    .home__author {
      margin: 0 auto 20px;
    }

    .home__content {
      &_start {
        padding: 0;
        text-align: center;
      }

      &_name {
        font-size: 29px;
        text-align: center;
      }

      &_position {
        margin-bottom: 30px;
        text-align: center;
      }

      .home__content_advantages {
        margin-bottom: 30px;
      }
    }

    .home__contacts {
      justify-content: center;

      .listContacts {
        margin-right: 10px;
      }
    }
  }
}
/*     < 991    */

/*     < 768    */
@include small-tablet() {
  .home {
    .home__content {
      &_name {
        font-size: 26px;
      }

      &_position {
        font-size: 22px;
      }
    }

    .home__contacts {
      flex-direction: column;

      .listContacts {
        flex-direction: column;
        margin-right: 0;
        margin-bottom: 20px;

        li {
          margin: 0 0 10px;
        }
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .home {
    .home__author {
      width: 290px;
      height: 290px;
    }

    .home__content {
      &_position {
        font-size: 20px;
      }

      &_text {
        & ~ .home__content_text {
          margin-top: 10px;
        }
      }
    }
  }
}
/*  <    575    */
/*   media   end */
