@import "assets/scss/variables";
@import "assets/scss/mixins";

.certificate__image {
  width: 80%;
  margin: 20px auto;
}

.certificate__text {
  text-align: center;
  font-size: $textSize;
}

.certificate__buttons {
  @include dflex(center, stretch, row, wrap);

  margin: 40px auto 0;

  &_backButton,
  &_link {
    display: block;
    width: fit-content;
    max-width: 100%;
    margin: 10px;
    padding: 10px 40px;
    font-weight: bold;
    font-size: $textSize;
    border-radius: $borderRadius;
    border: 2px solid $white;
    color: $white;
    background-color: $mainDarkColor;

    &:hover {
      color: $mainDarkColor;
      background-color: $white;
    }

    &:active {
      box-shadow: inset 0 0 10px $mainDarkColor;
    }
  }
}

/*   media   start */
/*     < 768    */
@include small-tablet() {
  .certificate__buttons {
    &_backButton,
    &_link {
      padding: 8px 30px;
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .certificate__buttons {
    margin-top: 30px;

    &_backButton,
    &_link {
      padding: 8px 30px;
    }
  }
}
/*  <    575    */
/*   media   end */
