@import "assets/scss/mixins";
@import "assets/scss/variables";

.wrapper {
  width: calc(100% - 80px);
  max-width: $maxContentWidth;
  margin: 0 auto;
}

/*   media   start */
/*     < 991    */
@include tablet() {
  .wrapper {
    width: calc(100% - 40px);
  }
}
/*     < 991    */

/*  <    575    */
@include mobile() {
  .wrapper {
    width: calc(100% - 20px);
  }
}
/*  <    575    */
/*   media   end */
