@import "assets/scss/variables";
@import "assets/scss/mixins";

.socialList {
  @include dflex(flex-start);

  &__item {
    list-style-type: none;

    &:not(:last-child) {
      margin-right: 25px;
    }
  }

  &__link {
    width: 40px;
    height: 40px;
    border: 2px solid $white;
    border-radius: $borderRadius;
    box-shadow: 0 0 10px $lightOrange;

    &:hover {
      transform: scale(1.2);
    }
  }
}


/*   media   start */
/*     < 991    */
@include tablet() {
  .socialList {
    &__item {
      &:not(:last-child) {
        margin-right: 15px;
      }
    }

    &__link {
      width: 35px;
      height: 35px;
    }
  }
}
/*     < 991    */

/*  <    575    */
@include mobile() {
}
/*  <    575    */
/*   media   end */
