@import "assets/scss/variables";
@import "assets/scss/mixins";

$cursorBigSize: 46px;

body.customCursor *,
body.customCursor *:before,
body.customCursor *:after {
  cursor: none !important;
}

.cursorInner,
.cursorOuter {
  position: fixed;
  z-index: 1000;
  border-radius: 50%;
  user-select: none;
  pointer-events: none;

  &.cursorOver {
    opacity: 0 !important;
    z-index: -1000;
  }
}

.cursorOuter {
  width: $cursorBigSize;
  height: $cursorBigSize;
  background-color: transparentize($white, 0.8);
  transition: background-color calc($transition * 2);
  will-change: transform;
  &.hidden {
    z-index: -100;
  }
}

.cursorInner {
  top: -100px;
  left: -100px;
  display: flex;
  width: $cursorBigSize;
  height: $cursorBigSize;
  background-color: $withoutColor;
  mix-blend-mode: difference;
  will-change: left, top;

  .cursorInner__ball {
    margin: auto;
    width: inherit;
    height: inherit;
    border-radius: 50%;
    background-color: $white;
    transform: scale(0.6);
    transition: transform 0.2s;
  }

  &.cursorHover .cursorInner__ball {
    transform: scale(0.9);
  }

  &.cursorClick .cursorInner__ball {
    transform: scale(1);
  }
}
