@mixin dflex($jc: center, $ai: stretch, $fd: row, $wrap: wrap) {
  display: flex;
  flex-direction: $fd;
  align-items: $ai;
  justify-content: $jc;
  flex-wrap: $wrap;
}

@mixin font-face($font-family, $file-path, $weight: normal, $style: normal) {
  @font-face {
    src: url("#{$file-path}.woff2") format("woff2"), url("#{$file-path}.woff") format("woff"),
      url("#{$file-path}.otf") format("opentype"), url("#{$file-path}.ttf") format("truetype");
    font-family: $font-family;
    font-weight: $weight;
    font-style: $style;
  }
}

@mixin psevdo($top: 0, $zIndex: -1, $content: "", $pos: absolute) {
  content: $content;
  position: $pos;
  top: $top;
  z-index: $zIndex;
}

@mixin animationPlay($name, $playDurationTime: 1s, $delay: 0s, $direction: normal, $easingFunction: ease) {
  animation: $name $playDurationTime $easingFunction $delay both $direction;
}

@mixin xxl-desktop() {
  @media screen and (max-width: 1300px) {
    @content;
  }
}

@mixin desktop() {
  @media screen and (max-width: 1100px) {
    @content;
  }
}

@mixin tablet() {
  @media screen and (max-width: 991px) {
    @content;
  }
}

@mixin small-tablet() {
  @media screen and (max-width: 768px) {
    @content;
  }
}

@mixin mobile() {
  @media screen and (max-width: 575px) {
    @content;
  }
}
