@import "assets/scss/variables";
@import "assets/scss/mixins";

$size_without_header_and_footer: calc(100vh - var(--header-size) - var(--footer-size));
$height_animation_block: max($size_without_header_and_footer, 50vh);

$animation_hide_content_time: 0.1s;
$curtain_transition: 0.3s;

$animation_curtain_time: 0.3s;
$delay_curtain_item_time1: 0.2s;
$delay_curtain_item_time2: 0.4s;

$animation_scale_time: 1.5s;
$delay_scale_time: calc($delay_curtain_item_time2 + $animation_curtain_time);
$full_animation_scale_time: calc($animation_scale_time + $delay_scale_time);

@keyframes translateLeftStart {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes translateLeftEnd {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes translateRightStart {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes translateRightEnd {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100%);
  }
}

@keyframes showContent {
  0% {
    opacity: 0;
    height: $height_animation_block;
    overflow: hidden;
  }

  50% {
    height: $height_animation_block;
    overflow: hidden;
  }

  100% {
    opacity: 1;
    height: auto;
    overflow: inherit;
  }
}

@keyframes hideContent {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes scaleCurtain {
  10% {
    transform: scale(1, 0.005);
  }

  35% {
    opacity: 1;
    transform: scale(0.05, 0.005);
  }

  50% {
    opacity: 0;
    transform: scale(0.05, 0.005);
  }

  65% {
    opacity: 1;
    transform: scale(1, 0.005);
  }

  100% {
    transform: scale(1, 1);
  }
}

@keyframes hideCurtainWidth {
  0% {
    z-index: 100;
    width: 50%;
  }

  95% {
    z-index: 100;
  }

  100% {
    width: 0;
    z-index: -100;
  }
}

.main {
  position: relative;
  z-index: 1;
  flex-grow: 1;
  margin-top: var(--header-size);
}

.curtain {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -100;
  width: 100%;
  height: $height_animation_block;
  max-height: 100vh;
  overflow: hidden;

  .curtain__left,
  .curtain__right {
    position: absolute;
    top: 0;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 0;
    height: 100%;
    overflow: hidden;
    transition: width $curtain_transition;
  }

  .curtain__item {
    flex-grow: 1;
    width: 100%;
    will-change: transform;
  }

  .curtain__left {
    left: 0;

    .curtain__item {
      @extend %bg-curtain1;

      & ~ .curtain__item {
        transform: translateX(-100%);
      }
    }
  }

  .curtain__right {
    right: 0;

    .curtain__item {
      @extend %bg-curtain2;

      &:not(:last-child) {
        transform: translateX(100%);
      }
    }
  }
}

// page is exiting and entering
.pageExit,
.pageEnter {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -10;
  width: 100%;
  opacity: 0;
  overflow: hidden;
}

// active time animation
.pageExitActive > section {
  transform: translateY(var(--top-section));
}

.pageExitActive {
  @include animationPlay(hideContent, $animation_hide_content_time, $delay_scale_time);
}

.pageEnterActive,
.pageExitActive {
  height: $height_animation_block;
  max-height: 100vh;
  overflow: hidden;

  & ~ .curtain {
    @include animationPlay(scaleCurtain, $animation_scale_time, $delay_scale_time);

    z-index: 100;

    .curtain__left {
      width: 50%;

      .curtain__item:nth-child(2) {
        @include animationPlay(translateLeftStart, $animation_curtain_time, $delay_curtain_item_time1);
      }

      .curtain__item:nth-child(3) {
        @include animationPlay(translateLeftStart, $animation_curtain_time, $delay_curtain_item_time2);
      }
    }

    .curtain__right {
      width: 50%;

      .curtain__item:nth-child(1) {
        @include animationPlay(translateRightStart, $animation_curtain_time, $delay_curtain_item_time1);
      }

      .curtain__item:nth-child(2) {
        @include animationPlay(translateRightStart, $animation_curtain_time, $delay_curtain_item_time2);
      }
    }
  }
}

// animations ended
.pageEnterDone {
  @include animationPlay(showContent, calc($animation_curtain_time), calc($delay_scale_time + 0.1s));

  & ~ .curtain {
    .curtain__left,
    .curtain__right {
      @include animationPlay(hideCurtainWidth, $animation_curtain_time, $delay_curtain_item_time2);
    }

    .curtain__left {
      .curtain__item:nth-child(2) {
        @include animationPlay(translateLeftEnd, $animation_curtain_time);
      }

      .curtain__item:nth-child(3) {
        @include animationPlay(translateLeftEnd, $animation_curtain_time, $delay_curtain_item_time1);
      }
    }

    .curtain__right {
      .curtain__item:nth-child(1) {
        @include animationPlay(translateRightEnd, $animation_curtain_time);
      }

      .curtain__item:nth-child(2) {
        @include animationPlay(translateRightEnd, $animation_curtain_time, $delay_curtain_item_time1);
      }
    }
  }
}
