@import "assets/scss/mixins";
@import "assets/scss/variables";

.padding {
  padding: $pagePadding;
  background-color: $mainDarkColor;
  background-image: $mainGradient;
}

/*  < 991 */
@include tablet {
  .padding {
    padding: $pagePaddingTablet;
  }
}
/*  < 991 */

/*  < 575 */
@include mobile {
  .padding {
    padding: $pagePaddingMobile;
  }
}
/*  < 575 */
