@import "assets/scss/variables";
@import "assets/scss/mixins";

.advantagesList {
  @include dflex(flex-start);

  &__item {
    @include dflex(flex-start, center);

    width: fit-content;
    max-width: 100%;
    color: $grey;

    &:not(:last-child) {
      margin-right: 80px;
    }

    &_value {
      font: bold 40px / 1 PressStart2P, sans-serif;
      margin-right: 10px;
      color: $green;
    }

    &_text {
      max-width: 220px;
      font-size: 18px;
      text-transform: uppercase;
    }
  }
}

/*   media   start */
/*     < 1300    */
@include xxl-desktop() {
  .advantagesList {
    &__item:not(:last-child) {
      margin-right: 1vw;
    }
  }
}
/*     < 1300    */

/*     < 1100    */
@include desktop() {
  .advantagesList {
    &__item {
      &_value {
        font-size: 34px;
      }

      &_text {
        font-size: 16px;
      }
    }
  }
}
/*     < 1100    */

/*     < 991    */
@include tablet() {
  .advantagesList {
    justify-content: center;

    &__item {
      &:not(:last-child) {
        margin-right: 1.5vw;
      }
    }
  }
}
/*     < 991    */

/*  <    575    */
@include mobile() {
  .advantagesList {
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__item {
      margin-bottom: 10px;

      &:not(:last-child) {
        margin-right: 0;
      }

      &_value {
        font-size: 30px;
        margin-right: 5px;
      }
    }
  }
}
/*  <    575    */
/*   media   end */
