@import "assets/scss/variables";
@import "assets/scss/mixins";

.coursesList {
  margin-top: 50px;

  &__item {
    padding-top: 180px;

    &:hover {
      &:before {
        opacity: 0;
      }
    }

    &_content {
      @include dflex(flex-start, center, row, wrap);

      font-size: $textSize;

      .separator {
        margin: 0 5px;
      }

      .name {
        width: 100%;
        font-size: 20px;
        color: $green;
      }
    }
  }
}


/*   media   start */
/*     < 991    */
@include tablet() {
  .coursesList {
    margin-top: 30px;

    &__item {
      padding-top: 140px;
    }
  }
}
/*     < 991    */

/*     < 768    */
@include small-tablet() {
  .coursesList {
    &__item {
      padding-top: 100px;

      &_content {
        .name {
          font-size: 18px;
        }
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .coursesList {
    max-width: 350px;
    margin: 20px auto 0;

    &__item {
      padding-top: 100px;
    }
  }
}
/*  <    575    */
/*   media   end */
