@import "assets/scss/variables";
@import "assets/scss/mixins";

.languagesList {
  @include dflex(center, stretch, row, wrap);

  margin-top: 50px;

  &__item {
    @extend %bg-dark;

    margin: 0 20px 30px;
    padding: 15px 40px;
    width: fit-content;
    max-width: 100%;
    line-height: 1;
    text-align: center;
    border-radius: $borderRadius;
    list-style-type: none;

    &_name {
      font-size: 20px;
    }

    &_value {
      margin-top: 10px;
      font-weight: bold;
      font-size: 26px;
      color: $orange;
    }
  }
}

/*   media   start */
/*     < 768    */
@include small-tablet() {
  .languagesList {
    margin-top: 40px;

    &__item {
      margin-left: 10px;
      margin-right: 10px;
      padding: 15px 30px;

      &_value {
        font-size: 24px;
      }
    }
  }
}
/*     < 768    */

/*  <    575    */
@include mobile() {
  .languagesList {
    margin-top: 30px;

    &__item {
      padding: 10px 20px;

      &_name {
        font-size: 18px;
      }

      &_value {
        margin-top: 10px;
        font-size: 22px;
      }
    }
  }
}
/*  <    575    */
/*   media   end */
